@font-face {
  font-family: "ralewayblack";
  src: url("./font/raleway-black-webfont.woff") format("woff"),
    url("./font/raleway-black-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "ralewayblack", Arial, sans-serif;
  margin: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
